.wrapper-app {
  display: grid;
  height: 100vh;
  grid-template-areas:
  "header header header"
  "navbar main statusbar"
  "footer footer footer";
  grid-template-columns: 1fr 9fr 1fr;
  grid-template-rows: 1fr 9fr 1fr;
  background-color: mainStyles.$color-white;
  color: mainStyles.$color-black;
}

.main {
  grid-area: main;
  font-family: 'Roboto', sans-serif;
  overflow: auto;
  background: url("img/Frame.png") no-repeat 0;
  background-size: 100% 100%;
  padding: 50px 76px;
}
